.el-tag{
    background-color: rgb(131, 136, 165);
    display: inline-block;
    padding: 0 5px;
    height: 24px;
    line-height: 22px;
    font-size: 12px;
    color: #fff;
    border-radius: 4px;
    box-sizing: border-box;
    border: 1px solid transparent;
    white-space: nowrap;
}
.el-tag .el-icon-close{
    border-radius: 50%;
    text-align: center;
    position: relative;
    cursor: pointer;
    font-size: 12px;
    transform: scale(.75, .75);
    height: 18px;
    width: 18px;
    line-height: 18px;
    vertical-align: middle;
    top: -1px;
    right: -2px;
}
.el-tag .el-icon-close:hover{
    background-color: #fff;
    color: rgb(131, 136, 165);
}
.el-tag--gray{
    background-color: rgb(228, 228, 241);
    border-color: rgb(228, 228, 241);
    color: rgb(72, 78, 106);
}
.el-tag--gray .el-tag__close:hover{
    background-color: rgb(72, 78, 106);
    color: #fff;
}
.el-tag--gray.is-hit{
    border-color: rgb(72, 78, 106);
}
.el-tag--primary{
    background-color: rgba(84, 136, 249, 0.1);
    border-color: rgba(84, 136, 249, 0.2);
    color: #5488F9;
}
.el-tag--primary .el-tag__close:hover{
    background-color: #5488F9;
    color: #fff;
}
.el-tag--primary.is-hit{
    border-color: #5488F9;
}
.el-tag--success{
    background-color: rgba(18,206,102,0.10);
    border-color: rgba(18,206,102,0.20);
    color: #13ce66;
}
.el-tag--success .el-tag__close:hover{
    background-color: #13ce66;
    color: #fff;
}
.el-tag--success.is-hit{
    border-color: #13ce66;
}
.el-tag--warning{
    background-color: rgba(247,186,41,0.10);
    border-color: rgba(247,186,41,0.20);
    color: #f7ba2a;
}
.el-tag--warning .el-tag__close:hover{
    background-color: #f7ba2a;
    color: #fff;
}
.el-tag--warning.is-hit{
    border-color: #f7ba2a;
}
.el-tag--danger{
    background-color: rgba(255,73,73,0.10);
    border-color: rgba(255,73,73,0.20);
    color: #ff4949;
}
.el-tag--danger .el-tag__close:hover{
    background-color: #ff4949;
    color: #fff;
}
.el-tag--danger.is-hit{
    border-color: #ff4949;
}
