.el-table-column{}

.el-table-column--selection .cell{
    padding-left: 14px;
    padding-right: 14px;
}

.el-table-filter{
    border: solid 1px rgb(209, 213, 229);
    border-radius: 2px;
    background-color: #fff;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .12);
    box-sizing: border-box;
    margin: 2px 0;
    /** used for dropdown mode */
}

.el-table-filter__list{
    padding: 5px 0;
    margin: 0;
    list-style: none;
    min-width: 100px;
}

.el-table-filter__list-item{
    line-height: 36px;
    padding: 0 10px;
    cursor: pointer;
    font-size: 14px;
}

.el-table-filter__list-item:hover{
    background-color: rgb(228, 228, 241);
    color: rgb(72, 78, 106);
}

.el-table-filter__list-item.is-active{
    background-color: #5488F9;
    color: #fff;
}

.el-table-filter__content{
    min-width: 100px;
}

.el-table-filter__bottom{
    border-top: 1px solid rgb(209, 213, 229);
    padding: 8px;
}

.el-table-filter__bottom button {
    background: transparent;
    border: none;
    color: rgb(131, 136, 165);
    cursor: pointer;
    font-size: 14px;
    padding: 0 3px
}

.el-table-filter__bottom button:hover{
    color: #5488F9;
}

.el-table-filter__bottom button:focus{
    outline: none;
}

.el-table-filter__bottom button.is-disabled{
    color: rgb(191, 196, 217);
    cursor: not-allowed;
}

.el-table-filter__checkbox-group{
    padding: 10px;
}

.el-table-filter__checkbox-group .el-checkbox {
    display: block;
    margin-bottom: 8px;
    margin-left: 5px;
}

.el-table-filter__checkbox-group .el-checkbox:last-child {
    margin-bottom: 0;
