}
@-moz-document url-prefix() {
  /*Firefox*/
body {
    width: 98vw;
}
}
::-webkit-scrollbar {
  width: 0.5vw;
  height: 0.5vw;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
