.save-btn,
.cancel-btn {
  display: inline-block;
  width: 140px;
  height: 50px;
  line-height: 50px;
  border: 1px solid #CBD6E3;
  margin: 20px 30px;
  cursor: pointer;
  border-radius: 4px;
  font-size: 16px;
}
.cancel-btn {
  background: white;
  color: #4F6D95;
}
.save-btn {
  background: #5488F9;
  color: white;
}
.btn-blue.el-button,
.btn-trans.el-button {
  margin-top: 5px;
  min-width: 80px;
}
.btn-blue.el-button {
  padding: 6px 11px;
}
.btn-trans.el-button {
  padding: 6px 11px;
  color: #4F6D95;
  border: 1px solid #CBD6E3;
}
.btn-trans.el-button:hover {
  background: #5488F9;
  color: white;
}
.el-button--text:hover {
  color: #5488F9;
  text-decoration: underline;
}
.logistic-container {
  width: 100%;
  margin-top: 20px;
  padding-bottom: 20px;
  min-height: 600px;
  background: white;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.08);
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 30px;
}
.fresh-btn.el-button {
  height: 35px;
  width: 35px;
  line-height: 39px;
  padding: 0;
  border: 1px solid #CBD6E3;
  vertical-align: middle;
}
.fresh-btn.el-button .icon-fresh {
  font-size: 20px;
  color: #B0C1D4;
  vertical-align: top;
}
.fresh-btn.el-button:hover .icon-fresh,
.fresh-btn.el-button:focus .icon-fresh {
  color: #5488F9;
}
.confirm-btn .el-button {
  width: 80px;
