.el-date-table {
    font-size: 12px;
    min-width: 224px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.el-date-table td {
    width: 32px;
    height: 32px;
    box-sizing: border-box;
    text-align: center;
    cursor: pointer;
}
.el-date-table td.next-month, .el-date-table td.prev-month {
    color: #ddd;
}
.el-date-table td.today {
    color: #5488F9;
    position: relative;
}
.el-date-table td.today:before {
    content: " ";
    position: absolute;
    top: 0px;
    right: 0px;
    width: 0;
    height: 0;
    border-top: 0.5em solid #5488F9;
    border-left: .5em solid transparent;
}
.el-date-table td.available:hover {
    background-color: rgb(228, 228, 241);
}
.el-date-table td.in-range {
    background-color: rgb(221, 231, 254);
}
.el-date-table td.in-range:hover {
    background-color: rgb(193, 212, 253);
}
.el-date-table td.current:not(.disabled), .el-date-table td.start-date, .el-date-table td.end-date {
    background-color: #5488F9 !important;
    color: #fff;
}
.el-date-table td.disabled {
    background-color: #f4f4f4;
    opacity: 1;
    cursor: not-allowed;
    color: #ccc;
}
.el-date-table td.week {
    font-size: 80%;
    color: rgb(131, 136, 165);
}
.el-date-table th {
    padding: 5px;
    color: rgb(131, 136, 165);
    font-weight: 400;
}
.el-date-table.is-week-mode .el-date-table__row {}
.el-date-table.is-week-mode .el-date-table__row:hover {
    background-color: rgb(228, 228, 241);
}
.el-date-table.is-week-mode .el-date-table__row.current {
    background-color: rgb(221, 231, 254);
}
