.el-picker-panel {
    color: rgb(72, 78, 106);
    border: 1px solid rgb(209, 213, 229);
    box-shadow: 0 2px 6px #ccc;
    background: #fff;
    border-radius: 2px;
    line-height: 20px;
    margin: 5px 0;
}
.el-picker-panel__body, .el-picker-panel__body-wrapper {}
.el-picker-panel__body::after, .el-picker-panel__body-wrapper::after {
    content: "";
    display: table;
    clear: both;
}
.el-picker-panel__content {
    position: relative;
    margin: 15px;
}
.el-picker-panel__footer {
    border-top: 1px solid #e4e4e4;
    padding: 4px;
    text-align: right;
    background-color: #fff;
    position: relative;
}
.el-picker-panel__shortcut {
    display: block;
    width: 100%;
    border: 0;
    background-color: transparent;
    line-height: 28px;
    font-size: 14px;
    color: rgb(72, 78, 106);
    padding-left: 12px;
    text-align: left;
    outline: none;
    cursor: pointer;
}
.el-picker-panel__shortcut:hover {
    background-color: rgb(228, 228, 241);
}
.el-picker-panel__shortcut.active {
    background-color: #e6f1fe;
    color: #5488F9;
}
.el-picker-panel__btn {
    border: 1px solid #dcdcdc;
    color: #333;
    line-height: 24px;
    border-radius: 2px;
    padding: 0 20px;
    cursor: pointer;
    background-color: transparent;
    outline: none;
    font-size: 12px;
}
.el-picker-panel__btn[disabled] {
    color: #cccccc;
    cursor: not-allowed;
}
.el-picker-panel__icon-btn {
    font-size: 12px;
    color: rgb(151, 157, 190);
    border: 0;
    background: transparent;
    cursor: pointer;
    outline: none;
    margin-top: 3px;
}
.el-picker-panel__icon-btn:hover {
    color: #5488F9;
}
.el-picker-panel__link-btn {
    cursor: pointer;
    color: #5488F9;
    text-decoration: none;
    padding: 15px;
    font-size: 12px;
}
.el-picker-panel *[slot=sidebar], .el-picker-panel__sidebar {
