    content: "";
    height: 100%;
    vertical-align: middle;
}
.el-upload-list--picture-card .el-upload-list__item-actions:after{
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle;
}
.el-progress-bar__inner:after{
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle;
