.el-upload{
    display: inline-block;
    text-align: center;
    cursor: pointer;
    /*  */
}

.el-upload iframe{
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    opacity: 0;
    filter: alpha(opacity=0);
}

.el-upload__input{
    display: none;
}

.el-upload__tip{
    font-size: 12px;
    color: rgb(131, 136, 165);
    margin-top: 7px;
}

.el-upload--picture-card{
    background-color: #fbfdff;
    border: 1px dashed #c0ccda;
    border-radius: 6px;
    box-sizing: border-box;
    width: 148px;
    height: 148px;
    cursor: pointer;
    line-height: 146px;
    vertical-align: top;
}

.el-upload--picture-card i {
    font-size: 28px;
    color: #8c939d;
}

.el-upload--picture-card:hover{
    border-color: #5488F9;
    color: #5488F9;
}

.el-upload-dragger{
    background-color: #fff;
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    box-sizing: border-box;
    width: 360px;
    height: 180px;
    text-align: center;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.el-upload-dragger .el-upload__text{
    color: rgb(151, 157, 190);
    font-size: 14px;
    text-align: center
}

.el-upload-dragger .el-upload__text em{
    color: #5488F9;
    font-style: normal;
}

.el-upload-dragger .el-icon-upload{
    font-size: 67px;
    color: rgb(151, 157, 190);
    margin: 40px 0 16px;
    line-height: 50px;
}

.el-upload-dragger + .el-upload__tip{
    text-align: center;
}

.el-upload-dragger ~ .el-upload__files{
    border-top: 1px solid rgba(191, 196, 217, 0.2);
    margin-top: 7px;
    padding-top: 5px;
}

.el-upload-dragger:hover{
    border-color: #5488F9;
}

.el-upload-dragger.is-dragover{
    background-color: rgba(32, 159, 255, .06);
    border: 2px dashed #5488F9;
}

.el-upload-list{
    margin: 0;
    padding: 0;
    list-style: none;
}

.el-upload-list__item{
    transition: all .5s cubic-bezier(.55,0,.1,1);
    font-size: 14px;
    color: rgb(72, 78, 106);
    line-height: 1.8;
    margin-top: 5px;
    position: relative;
    box-sizing: border-box;
    border-radius: 4px;
    width: 100%;
    position: relative;
}

.el-upload-list__item .el-progress-bar {
    margin-right: 0;
    padding-right: 0;
}

.el-upload-list__item .el-progress{
    position: absolute;
    bottom: -3px;
    width: 100%;
}

.el-upload-list__item .el-progress__text{
    position: absolute;
    right: 0;
    top: -10px;
    right: 0;
}

.el-upload-list__item:first-child{
    margin-top: 10px;
}

.el-upload-list__item:hover{
    background-color: rgb(238, 239, 246);
}

.el-upload-list__item.is-success .el-upload-list__item-name:hover{
    color: #5488F9;
    cursor: pointer;
}

.el-upload-list__item.is-success .el-icon-close{
    display: none;
}

.el-upload-list__item.is-success:hover .el-icon-close{
    display: inline-block;
    cursor: pointer;
    opacity: .75;
    transform: scale(.7);
    color: rgb(72, 78, 106);
}

.el-upload-list__item.is-success:hover .el-icon-close:hover{
    opacity: 1;
}

.el-upload-list__item.is-success:hover .el-icon-circle-check, .el-upload-list__item.is-success:hover .el-icon-check{
    display: none;
}

.el-upload-list__item-name{
    color: rgb(72, 78, 106);
    display: block;
    margin-right: 40px;
    overflow: hidden;
    padding-left: 4px;
    text-overflow: ellipsis;
    transition: color .3s;
    white-space: nowrap;
}

.el-upload-list__item-name [class^="el-icon"] {
    color: rgb(151, 157, 190);
    margin-right: 7px;
    height: 100%;
    line-height: inherit;
}

.el-upload-list__item-status-label{
    position: absolute;
    right: 10px;
    top: 0;
    line-height: inherit;
    color: #13ce66;
}

.el-upload-list__item-delete{
    position: absolute;
    right: 10px;
    top: 0;
    font-size: 12px;
    color: rgb(72, 78, 106);
    display: none;
}

.el-upload-list__item-delete:hover{
    color: #5488F9;
}

.el-upload-list--picture-card{
    margin: 0;
    display: inline;
    vertical-align: top;
}

.el-upload-list--picture-card .el-upload-list__item {
    overflow: hidden;
    background-color: #fff;
    border: 1px solid #c0ccda;
    border-radius: 6px;
    box-sizing: border-box;
    width: 148px;
    height: 148px;
    margin: 0 8px 8px 0;
    display: inline-block
}

.el-upload-list--picture-card .el-upload-list__item:hover .el-upload-list__item-status-label{
    display: none;
}

.el-upload-list--picture-card .el-upload-list__item-name {
    display: none;
}

.el-upload-list--picture-card .el-upload-list__item-thumbnail {
    width: 100%;
    height: 100%;
}

.el-upload-list--picture-card .el-upload-list__item-status-label {
    position: absolute;
    right: -15px;
    top: -6px;
    width: 40px;
    height: 24px;
    background: #13ce66;
    text-align: center;
    transform: rotate(45deg);
    box-shadow: 0 0 1pc 1px rgba(0,0,0,0.2);
}

.el-upload-list--picture-card .el-upload-list__item-status-label i {
    font-size: 12px;
    margin-top: 11px;
    transform: rotate(-45deg) scale(0.8);
    color: #fff;
}

.el-upload-list--picture-card .el-upload-list__item-actions {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    cursor: default;
    text-align: center;
    color: #fff;
    opacity: 0;
    font-size: 20px;
    background-color: rgba(0, 0, 0, .5);
    transition: opacity .3s;
}

.el-upload-list--picture-card .el-upload-list__item-actions span {
    display: none;
    cursor: pointer;
}

.el-upload-list--picture-card .el-upload-list__item-actions span + span {
    margin-left: 15px;
}

.el-upload-list--picture-card .el-upload-list__item-actions .el-upload-list__item-delete {
    position: static;
    font-size: inherit;
    color: inherit;
}

.el-upload-list--picture-card .el-upload-list__item-actions:hover{
    opacity: 1;
}

.el-upload-list--picture-card .el-upload-list__item-actions:hover span{
    display: inline-block;
}

.el-upload-list--picture-card .el-progress {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    bottom: auto;
    width: 126px;
}

.el-upload-list--picture-card .el-progress .el-progress__text {
    top: 50%;
}

.el-upload-list--picture .el-upload-list__item {
    overflow: hidden;
    background-color: #fff;
    border: 1px solid #c0ccda;
    border-radius: 6px;
    box-sizing: border-box;
    margin-top: 10px;
    padding: 10px 10px 10px 90px;
    height: 92px
}

.el-upload-list--picture .el-upload-list__item:hover .el-upload-list__item-status-label{
    background: transparent;
    box-shadow: none;
    top: -2px;
    right: -12px;
}

.el-upload-list--picture .el-upload-list__item:hover .el-upload-list__item-status-label .el-icon-close{
    transform: rotate(45deg) scale(.7);
}

.el-upload-list--picture .el-upload-list__item.is-success .el-upload-list__item-name{
    line-height: 70px;
    margin-top: 0;
}

.el-upload-list--picture .el-upload-list__item.is-success .el-upload-list__item-name i{
    display: none;
}

.el-upload-list--picture .el-upload-list__item-thumbnail {
    vertical-align: middle;
    display: inline-block;
    width: 70px;
    height: 70px;
    float: left;
    margin-left: -80px;
}

.el-upload-list--picture .el-upload-list__item-name {
    display: block;
    margin-top: 20px;
}

.el-upload-list--picture .el-upload-list__item-name i {
    font-size: 70px;
    line-height: 1;
    position: absolute;
    left: 9px;
    top: 10px;
}

.el-upload-list--picture .el-upload-list__item-status-label {
    position: absolute;
    right: -17px;
    top: -7px;
    width: 46px;
    height: 26px;
    background: #13ce66;
    text-align: center;
    transform: rotate(45deg);
    box-shadow: 0 1px 1px #ccc;
}

.el-upload-list--picture .el-upload-list__item-status-label i {
    font-size: 12px;
    margin-top: 12px;
    transform: rotate(-45deg) scale(0.8);
    color: #fff;
}

.el-upload-list--picture .el-progress {
    position: static;
}

.el-upload-cover{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 10;
    cursor: default;
}

.el-upload-cover img{
    display: block;
    width: 100%;
    height: 100%;
}

.el-upload-cover + .el-upload__inner{
    opacity: 0;
    position: relative;
    z-index: 1;
}

.el-upload-cover__label{
    position: absolute;
    right: -15px;
    top: -6px;
    width: 40px;
    height: 24px;
    background: #13ce66;
    text-align: center;
    transform: rotate(45deg);
    box-shadow: 0 0 1pc 1px rgba(0,0,0,0.2);
}

.el-upload-cover__label i {
    font-size: 12px;
    margin-top: 11px;
    transform: rotate(-45deg) scale(0.8);
    color: #fff;
}

.el-upload-cover__progress{
    display: inline-block;
    vertical-align: middle;
    position: static;
    width: 243px;
}

.el-upload-cover__progress + .el-upload__inner{
    opacity: 0;
}

.el-upload-cover__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.el-upload-cover__interact{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.72);
    text-align: center;
}

.el-upload-cover__interact .btn{
    display: inline-block;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
    vertical-align: middle;
    transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms, opacity 300ms cubic-bezier(0.23, 1, 0.32, 1) 100ms;
    margin-top: 60px;
}

.el-upload-cover__interact .btn i{
    margin-top: 0;
}

.el-upload-cover__interact .btn span{
    opacity: 0;
    transition: opacity .15s linear;
}

.el-upload-cover__interact .btn:not(:first-child){
    margin-left: 35px;
}

.el-upload-cover__interact .btn:hover{
    transform: translateY(-13px);
}

.el-upload-cover__interact .btn:hover span{
    opacity: 1;
}

.el-upload-cover__interact .btn i{
    color: #fff;
    display: block;
    font-size: 24px;
    line-height: inherit;
    margin: 0 auto 5px;
}

.el-upload-cover__title{
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #fff;
    height: 36px;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: 400;
    text-align: left;
    padding: 0 10px;
    margin: 0;
    line-height: 36px;
    font-size: 14px;
    color: rgb(72, 78, 106);
}
